import { Controller, Node, Block } from '@ali/4ever-cangjie';
import { TemplateButton } from '../../mo';

interface QueryData {
  node: Node | Block | null;
}
// 此处不能缓存父子关系， 拖拽会改变父子关系
export default function isInTemplateButton(
  controller: Controller,
  data: QueryData,
) {
  const { node } = data;
  if (!node) return false;
  let parent = controller.value.document.getParent(node.key);
  while (parent && !TemplateButton.isTemplateButton(parent)) {
    parent = controller.value.document.getParent(parent.key);
  }
  return !!parent;
}
